@charset "UTF-8";

@import "vendors/andy";


html, body {
  margin: 0px;
  padding: 0px;
  font-size: (16 / 14.4) * 1vw;
  line-height: normal;
  color: white;
  background-color: #b1020d;
  font-family: "Cooper Black";
  width: 100%;
  height: 100%;

  @import "vendors/animate";
}

* {
  overflow: hidden !important;
}

.show {
  display: block !important;
  visibility: visible !important;
}

canvas {
  background: transparent;
}

#core-container {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;

  > * {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
  }
}

#page-init {
  display: block;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: white;

  .text {
    display: block;
    position: absolute;
    width: 100%;
    text-align: center;
    font-size: 2rem;
    font-family: "Courier New";
    top: 50%;
    color: black;

    transform: translateY(-50%);

    .blank {
      animation-name: enter-animate;
      animation-duration: 1s;
      animation-iteration-count: infinite;
    }
  }

}

#page-landing {
  display: block;
  position: absolute;
  width: 100%;
  background: url(../images/bg.jpg) center center no-repeat;
  background-size: cover;
  top: 0px;
  left: 0px;
  height: 100%;

  .text-1,
  .text-2 {
    font-size: 12rem;
    color: white;
    display: inline-block;
    position: absolute;
    width: auto;
  }

  .text-1 {
    top: (100 / 16) * 1rem;
    left: 4rem;
  }

  .text-2 {
    top: (320 / 16) * 1rem;
    right: 4rem;
  }

  #carol {
    display: block;
    position: absolute;
    width: 25rem;
    top: 20rem;
    left: 20rem;

    animation-name: carol-animate;
    animation-duration: 1000ms;
    animation-iteration-count: infinite;
  }

  @keyframes carol-animate {
    0% {
      transform: rotate(0deg);
    }

    25% {
      transform: rotate(-30deg);
    }

    50% {
      transform: rotate(0deg);
    }

    75% {
      transform: rotate(30deg);
    }

    100% {
      transform: rotate(0deg);
    }
  }

  #go-btn {
    display: inline-block;
    font-size: 4rem;
    bottom: 5rem;
    right: 2rem;
    position: absolute;
    width: auto;
    cursor: pointer;
    text-shadow: 2px 2px yellow;

    animation-name: enter-animate;
    animation-duration: 1s;
    animation-iteration-count: infinite;
  }
}

#page-game {
  background-color: #9b020b;

  #money-bg {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: -100%;
    left: 0px;
    background-position: 0px 0px;
    background-repeat: repeat;
    background-image: url(../images/money.png);
    background-size: 40px auto;

    animation-name: bg-animate;
    animation-duration: 300ms;
    animation-iteration-count: infinite;
  }

  @keyframes bg-animate {
    0% {background-position: 0px 0px; opacity: 1;}
    50% {background-position: -20px -20px; opacity: 0.8;}
    100% {background-position: -40px -40px; opacity: 1;}
  }

  #tree {
    display: block;
    width: 100%;
    position: relative;
    height: 100%;
    background-image: url(../images/tree.png);
    background-position: center center;
    background-size: cover;
  }

  #counter {
    display: block;
    position: absolute;
    top: (50 / 14.4) * 1vw;
    left: (50 / 14.4) * 1vw;
    font-size: 6rem;
    line-height: normal;

    &.blink {
      animation-name: enter-animate;
      animation-duration: 1s;
      animation-iteration-count: infinite;
    }
  }

  #amount {
    display: block;
    position: absolute;
    text-align: right;
    top: 50%;
    right: (50 / 14.4) * 1vw;
    font-size: 6rem;
    line-height: normal;

    @include prefix(transform, translateY(-50%), 'webkit' 'ms');
  }

  #get-ready{
    display: none;
    position: absolute;
    text-align: center;
    top: 50%;
    left: 50%;
    border-radius: 1rem;
    background: rgba(255, 255, 255, 0.2);
    padding: 1rem;

    @include prefix(transform, translate(-50%, -50%), 'webkit' 'ms');

    .text {
      display: block;
      font-size: 4rem;
      line-height: normal;
      text-shadow: 2px 2px yellow;
    }

    .count {
      display: block;
      font-size: 5rem;
      line-height: normal;
      text-shadow: 2px 2px yellow;
    }
  }

  #press-enter {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    font-size: 5rem;
    text-shadow: 2px 2px yellow;
    background: rgba(255, 255, 255, 0.3);
    .text {
      text-align: center;
      display: block;
      width: 100%;
      position: absolute;
      bottom: 2rem;

      animation-name: enter-animate;
      animation-duration: 1s;
      animation-iteration-count: infinite;
    }
  }
}


#page-game-over {
  display: none;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: rgba(0,0,0,0.8);

  .wrapper {
    display: block;
    position: absolute;
    width: auto;
    height: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1rem solid white;
    border-radius: 1rem;
    padding: 4rem;
    background: url(../images/bg.jpg) center center;
    background-size: cover;
  }

  .title {
    display: block;
    position: relative;
    width: 100%;
    text-align: center;
    margin-bottom: 2rem;
    font-size: 5rem;
  }

  .you-won {
    display: block;
    position: relative;
    width: 100%;
    text-align: center;
    margin-bottom: 2rem;
    font-size: 3rem;
  }

  .won {
    display: block;
    position: relative;
    width: 100%;
    text-align: center;
    font-size: 3rem
  }

  .bonus-wrapper,
  .total {
    display: none;
  }

  .bonus-wrapper {
    position: relative;
    width: 80%;
    padding: 1rem;
    border-radius: 1rem;
    background-color: rgba(255, 255, 255, 0.8);
    color: #FF0000;
    margin: 2rem auto;

    .and,
    .bonus {
      display: block;
      position: relative;
      width: 100%;
      text-align: center;
      font-size: 2rem;
      line-height: normal;
    }
  }

  .total {
    position: relative;
    width: 100%;
    font-size: 5rem;
    margin: 0 auto;
    text-align: center;
  }

  &.has-bonus {
    .bonus-wrapper,
    .total {
      display: block;
      visibility: hidden;
    }
  }
}


#page-firework {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  top: 0px;
  left: 0px;
}
.final-amount {
  font-size: 10rem !important;
  -webkit-text-stroke: 0.5rem yellow;
  -webkit-text-fill-color: white;
  -webkit-animation: fill 0.5s infinite alternate;
}

@-webkit-keyframes fill {
  from { -webkit-text-fill-color: yellow; }
  to { -webkit-text-fill-color: white; }
}

@keyframes enter-animate {
  0% {opacity: 1;}
  50% {opacity: 0.5;}
  100% {opacity: 1;}
}
